.custom-style-selection-top-performed-procedures > .ant-select-selector {
  border-radius: 8px !important;
  border: 1px solid #6b43b5 !important;
  color: #6b43b5;
  padding: 6px 12px !important;
  align-items: center;
}

.custom-scroll {
  scrollbar-width: thin;
  scrollbar-color: #6b43b5 transparent;
}

.fixed-body-height-scroll > .ant-spin-nested-loading > .ant-spin-container > .ant-table > .ant-table-container > .ant-table-body {
  height: 178px;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #6b43b5 transparent;
}

.top-treatment-carousel-dots > li:not(.slick-active) {
  background-color: #c9c1f1;
  width: 7px !important;
}
.top-treatment-carousel-dots > li.slick-active {
  background-color: #6b43b5;
}

.chart-pagination {
  width: 7px;
  height: 4px;
  background-color: #c9c1f1;
  border-radius: 100px;
  transition: all 0.3s;
  cursor: pointer;
}

.chart-pagination-active {
  width: 70px;
  background-color: #6b43b5;
}
