.circle {
  height: 82px;
  width: 82px;
  border-radius: 100%;
  background: #7F59CE;
  filter: opacity(25%);
  margin-left: 25px;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;

}

.icon-center {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
}

.div-space-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.div-start {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 60px;
}

.main-contianer {
  flex: 1 1 0%;
  display: flex;
  flex-direction: column;
}

.card-layout {
  /* padding: 1.5rem; */
  flex: 1 1 0%;
  display: flex;
  flex-direction: column;
}

.card-layout > * + * {
  margin-top: 2rem;
}

.responsive-section > * + * {
  margin-top: 32px;
}

@media (min-width: 768px) {
  .responsive-section {
      padding-left: 40px;
      padding-right: 40px;
  }
}

@media (min-width: 1024px) {
  .responsive-section {
    padding-left: 64px;
    padding-right: 64px;
  }
}

/* Main container */
.flex-container {
  flex: 1 1 0%;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 24px;
  padding-bottom: 24px;
}

@media (min-width: 768px) {
  .flex-container {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media (min-width: 1024px) {
  .flex-container {
    padding-left: 64px;
    padding-right: 64px;
  }
}

/* Top row layout */
.top-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Logo container */
.logo-container {
  display: flex;
  align-items: center;
  gap: 8px;
}

/* Logo image */
.logo-image {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  object-fit: cover;
}

/* Fallback logo */
.fallback-logo {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: var(--brand-color, #000);
  color: white;
  display: grid;
  place-items: center;
  font-size: 14px;
  font-weight: 600;
}

/* Clinic name button */
.clinic-button {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 14px;
  font-weight: 500;
  color: #374151;
}

.clinic-button svg {
  width: 16px;
  height: 16px;
  color: #6B7280;
}

/* Select container */
.select-container {
    position: relative;
    width: 100%;
    /* box-shadow: 0 1px 2px -2px rgba(0, 0, 0, 0.16), 0 3px 6px 0 rgba(0, 0, 0, 0.12), 0 5px 12px 4px rgba(0, 0, 0, 0.09); */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
    border-radius: 12px;


}

/* Select element */
.custom-select {
  width: 100%;
  height: 48px;
  border-radius: 12px;
  border: none;
  background-color: white;
  padding-right: 40px;
  padding-left: 24px;
  font-size: 16px;
  color: #1F2937;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

.custom-select:focus {
  outline: none;
  box-shadow: 0 0 0 2px var(--brand-color, #000);
}

.top-margin-large {
  margin-top: 40px !important ;
}

.margin-left-small {
  margin-left: 20px !important ;
}

/* Styles for screens below md (768px) */
@media (max-width: 767.98px) {
  .top-margin-large {
    margin-top: 20px !important; /* Smaller margin for mobile */
  }
  
  .margin-left-small {
    margin-left: 10px !important; /* Smaller margin for mobile */
  }
}