.insurance-item {
  display: flex;
  align-items: center;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  padding: 20px 30px;
  margin-top: 20px;
  border-radius: 7px;
  justify-content: space-between;
  cursor: pointer;
}

.treatment-name {
  width: 200px;
  display: block;
  font-size: 20px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  text-wrap: nowrap;
}

.files-submission-status-container {
  display: flex;
  align-items: start;
  justify-content: space-between;
  gap: 70px;
}

.file-label,
.submission-label,
.status-label {
  display: flex;
  align-items: center;
}

.view-button-after-break {
  display: none;
}

.view-button-before-break {
  display: flex;
  align-items: end;
}

.insurance-labels {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.insurance-labels-text {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 16px;
  height: 100%;
}

@media (max-width: 1700px) {
  .files-submission-status-container {
    gap: 40px;
  }
}

@media (max-width: 1500px) {
  .insurance-item {
    flex-direction: column;
    align-items: flex-start;
  }

  .insurance-name-after-break {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  .view-button-after-break {
    display: block;
  }

  .view-button-before-break {
    display: none;
    align-items: end;
  }

  .insurance-labels {
    max-width: 1000px;
  }

  .files-submission-status-container {
    width: 100%;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

@media (max-width: 1400px) {
  .insurance-item {
    flex-direction: column;
    align-items: flex-start;
  }

  .view-button-after-break {
    display: block;
  }

  .view-button-before-break {
    display: none;
  }
}

@media (max-width: 1130px) {
  .insurance-item {
    flex-direction: column;
    align-items: start;
  }

  .files-submission-status-container {
    flex-direction: column;
    align-items: start;
    gap: 10px;
    margin-top: 10px;
  }

  .file-label {
    width: 100%;
    justify-content: space-between;
  }

  .submission-label {
    width: 100%;
    justify-content: space-between;
  }

  .status-label {
    width: 100%;
    justify-content: space-between;
  }

  .view-button-before-break > button {
    margin-left: 0 !important;
    margin-top: 10px;
  }
}

@media (max-width: 650px) {
  .insurance-labels {
    flex-direction: column;
    align-items: start;
    justify-content: center;
    gap: 10px;
  }

  .insurance-labels-text {
    font-size: 14px;
  }
}

/* Request modal radio buttons */
.ant-radio-wrapper .ant-radio-checked .ant-radio-inner::after {
  background-color: white;
}

/* Submisison, Received Dates and Files Row */
.submission-received-dates-files {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}

.submission-received-dates-files-item {
  display: flex;
  gap: 5px;
  font-size: 16px;
  white-space: nowrap;
}

.post-payment-status {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

@media (max-width: 850px) {
  .post-payment-status {
    flex-direction: column;
    align-items: start;
    gap: 5px;
  }
}

@media (max-width: 1000px) {
  .submission-received-dates-files {
    flex-direction: column;
    align-items: start;
  }

  .submission-received-dates-files-item {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

@media (max-width: 650px) {
  .post-payment-status {
    flex-direction: column;
    align-items: start;
    gap: 5px;
  }

  .submission-received-dates-files {
    gap: 5px;
  }
}
@media (max-width: 620px) {
  .submission-received-dates-files {
    flex-direction: column;
    align-items: start;
  }
}
/* Single Insurance Submission Reveived Dates */
.submission-received-date {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 50%;
}

@media (max-width: 1450px) {
  .submission-received-date {
    flex-direction: column;
    width: 50%;
    align-items: start;
    gap: 5px;
  }
}

/* Insurance treatment table overflow */
.insurance-treatment-table-container
  > .ant-table-wrapper.css-dev-only-do-not-override-frcp1j
  > .ant-spin-nested-loading.css-dev-only-do-not-override-frcp1j
  > .ant-spin-container
  > .ant-table.css-dev-only-do-not-override-frcp1j
  > .ant-table-container
  > .ant-table-content {
  overflow-x: scroll;
}

.insurance-treatment-table-container {
  position: relative;
}
/* ///////////////////////////////////////////////////////// */
/* Filters */

.insurance-filter-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.predetermination-filters-inputs {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.insurance-filter-input {
  max-width: 590px;
  width: 90%;
  border: 1px solid #6b43b5;
  border-radius: 5px;
  height: 42px;
}
.insurance-filter-input > .ant-select-selector {
  border: none !important;
}
.insurance-filter-input > .ant-select-selector > .ant-select-selection-wrap {
  align-self: center !important;
}

.insurance-filters-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 30px;
}

.predetermination-filter-inputs-label {
  margin-bottom: 0;
  min-width: 90px;
}

.patient-treatment-insurance-estimate-inputs {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
}

.slider {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
}

.decrease-width {
  width: 90% !important;
}

.filter-input-mb {
  margin-bottom: 10px !important;
}

.second-col {
  margin-top: 20px;
  width: 99%; /* To fixed the right aligment for filters */
}

.divider {
  height: 80px;
  border: "1px solid rgba(183, 183, 183, 1)";
  margin-right: 20px;
  margin-left: -20px;
}

@media (max-width: 1300px) {
  .insurance-filters-container {
    display: flex;
    flex-direction: column;
  }

  .insurance-filter-row {
    flex-direction: column;
    align-items: start !important;
    gap: 10px;
  }

  .predetermination-filters-inputs {
    justify-content: space-between;
  }

  .patient-treatment-insurance-estimate-inputs {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    max-width: 100%;
    gap: 10px;
  }

  .insurance-filter-input {
    max-width: 788px;
    width: 100% !important;
  }

  .predetermination-filter-inputs-label {
    min-width: 100px;
  }

  .slider {
    margin-top: 0;
    max-width: 100%;
    width: 500px;
  }

  .divider {
    display: none;
    margin-left: 0;
  }

  .second-col {
    margin-top: 10px;
    width: 100%;
  }

  .decrease-width {
    width: 100% !important;
  }
}

@media (max-width: 650px) {
  .predetermination-filters-inputs,
  .patient-treatment-insurance-estimate-inputs {
    flex-direction: column;
    align-items: start;
    margin-bottom: 10px;
  }

  .predetermination-filter-inputs-label {
    margin-bottom: 0 !important;
  }
}

.post-payment-predetermination-tabs {
  border-radius: 100px;
  border: none;
  padding: 7px 40px;
  cursor: pointer;
  transition: all 0.1s;
}

.post-payment-predetermination-tabs.active {
  background-color: #6b43b5;
  color: white;
}

/* Status Selection */
.status-select {
  border-radius: 100px;
  border: none;
  padding: 0 10px !important;
}

.status-select > div {
  border: 0 !important;
  background: none !important;
}

div.pending > div.ant-select-selector > span.ant-select-selection-wrap > span.ant-select-selection-item > option {
  color: #ec942c;
  min-width: 90px;
}
div.approved > div.ant-select-selector > span.ant-select-selection-wrap > span.ant-select-selection-item > option {
  color: #04c900;
  min-width: 90px;
}
div.rejected > div.ant-select-selector > span.ant-select-selection-wrap > span.ant-select-selection-item > option {
  color: #ff0000;
  min-width: 90px;
}
div.in-progress > div.ant-select-selector > span.ant-select-selection-wrap > span.ant-select-selection-item > option {
  color: #848696;
  min-width: 90px;
}

/* ///////////////////////////////////////////////////////// */
/* Insurance AI Processing */
.insurance-ai-processing {
  backdrop-filter: blur(5px);
  z-index: 100;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  color: #6b43b5;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  gap: 3px;
  border: 2px solid rgba(5, 5, 5, 0.1);
  font-size: 18px;
}

.progressbar-container {
  display: flex;
  align-items: center;
  gap: 5px;
  width: 30%;
}

.insurance-ai-processing-text {
  margin-bottom: 0;
  font-weight: 500;
}

.insurance-text-container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

@media (max-width: 1600px) {
  .progressbar-container {
    width: 40%;
  }
}
@media (max-width: 1300px) {
  .progressbar-container {
    width: 50%;
  }
}
@media (max-width: 1100px) {
  .progressbar-container {
    width: 60%;
  }
}
@media (max-width: 1000px) {
  .progressbar-container {
    width: 55%;
  }
}
@media (max-width: 900px) {
  .progressbar-container {
    flex-direction: column;
    width: 80%;
    margin-top: 10px;
  }
}

@media (max-width: 900px) {
  .progressbar-container {
    flex-direction: column;
    width: 80%;
    margin-top: 10px;
  }

  .insurance-text-container {
    text-align: center;
  }
}

@media (max-width: 600px) {
  .insurance-ai-processing-text {
    text-wrap: wrap;
    font-size: 14px;
  }
}

@media (max-width: 1000px) {
  .insurance-ai-processing {
    font-size: 16px;
  }
}

/* //////////////////////////////////////////////////////// */
/* Select Patient ID */
.select-patient-id {
  width: 300px !important;
  min-width: 200px !important;
  height: 40px;
  border: 1px solid #6b43b5;
  border-radius: 7px;
}

.select-patient-id > .ant-select-selector {
  border: none !important;
}

.select-patient-id.no-transparent-background
  > .ant-select-selector
  > span.ant-select-selection-wrap
  > span.ant-select-selection-search
  > input.ant-select-selection-search-input {
  background-color: #f6f5fd;
  padding: 0 3px;
  width: 95%;
}

.select-patient-id
  > .ant-select-selector
  > span.ant-select-selection-wrap
  > span.ant-select-selection-search
  > input.ant-select-selection-search-input {
  background-color: transparent;
  padding: 0 3px;
  width: 95%;
}

.ant-select-dropdown.css-dev-only-do-not-override-frcp1j.ant-select-dropdown-placement-bottomLeft {
  padding: 0;
}

.select-patient-id > .ant-select-selector > span.ant-select-selection-wrap > span.ant-select-selection-placeholder {
  z-index: 10;
  padding: 0 3px;
}

/* //////////////////////////////////////////////////////// */
/* Insurance Proceudre */
td.ant-table-cell:has(> .inner-area-insurance-procedure) {
  background-color: #f6f5fd !important;
}
/* //////////////////////////////////////////////////////// */
/* Approved Button */
.approved-button {
  background-color: rgba(215, 255, 214, 1);
  color: #23d020;
}
