/* //////////////////////////////////////////////////////// */
/* Select Guarantor */
.ar-guarantor-dropdown {
  /* width: 300px !important;
  min-width: 200px !important; */
  height: 40px;
  border: 1px solid #6b43b5;
  border-radius: 7px;
}

.ar-guarantor-dropdown > .ant-select-selector {
  border: none !important;
}

.ar-guarantor-dropdown.no-transparent-background
  > .ant-select-selector
  > span.ant-select-selection-wrap
  > span.ant-select-selection-search
  > input.ant-select-selection-search-input {
  background-color: #f6f5fd;
  padding: 0 3px;
  width: 95%;
}

.ar-guarantor-dropdown
  > .ant-select-selector
  > span.ant-select-selection-wrap
  > span.ant-select-selection-search
  > input.ant-select-selection-search-input {
  background-color: transparent;
  padding: 0 3px;
  width: 95%;
}

.ar-guarantor-dropdown > .ant-select-selector > span.ant-select-selection-wrap > span.ant-select-selection-placeholder {
  z-index: 10;
  padding: 0 3px;
}
