/* //////////////////////////////////////////////////////// */
/* Breakdown AI blur table effect */
.breakdown-ai-processing {
  backdrop-filter: blur(5px);
  z-index: 100;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  color: #6b43b5;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  gap: 3px;
  border: 2px solid rgba(5, 5, 5, 0.1);
  font-size: 18px;
}

.breakdown-ai-processing-text {
  margin-bottom: 0;
  font-weight: 500;
}

.breakdown-text-container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.predetermination-spinner-container {
  backdrop-filter: blur(5px);
  z-index: 100;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.15);
}

/* //////////////////////////////////////////////////////// */
/* Predetermination approved view */
.approved-view {
  background-color: rgba(0, 0, 0, 0.1);
  position: absolute;
  z-index: 1000;
  backdrop-filter: blur(7px);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  animation: show-predetermination-approved-view 0.5s;
}

.approved-view-message {
  padding: 10px 20px;
  border-radius: 100px;
  background-color: rgba(4, 201, 0, 0.15);
  color: #04c900;
  font-size: 24px;
}

@keyframes show-predetermination-approved-view {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

