.treatment-financing-insurance-tabs {
  border-radius: 100px;
  border: none;
  padding: 20px 35px;
  cursor: pointer;
  transition: all 0.1s;
  width: 100%;
  color: #848696;
}

.treatment-financing-insurance-tabs:hover {
  background-color: #efefef !important;
}

.treatment-financing-insurance-tabs.active {
  background-color: #6b43b5;
  color: white;
}

/* Media queries for smaller screens */
@media (max-width: 767px) {
  .treatment-financing-insurance-tabs {
    padding: 12px 15px;
    font-size: 14px;
  }
}

@media (max-width: 575px) {
  .treatment-financing-insurance-tabs {
    padding: 10px 12px;
    font-size: 12px;
  }
}

.treatment-statistics-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
}

.treatment-statistics-first-row {
  width: 50%;
}

.show-below-1200 {
  display: none;
}

@media (max-width: 1200px) {
  .treatment-statistics-section {
    flex-direction: column;
  }

  .treatment-statistics-first-row {
    width: 100%;
  }

  .show-below-1200 {
    display: flex;
  }
}
