.fixed-body-height-scroll-top-treatments-by-plan-type
  > .ant-spin-nested-loading
  > .ant-spin-container
  > .ant-table
  > .ant-table-container
  > .ant-table-body {
  height: 172px;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #6b43b5 transparent;
}

.custom-style-selection-insurance-filter > .ant-select-selector {
  border-radius: 7px !important;
  padding: 18.5px 15px !important;
  border: 1px solid #6b43b5 !important;
}

.chart-pagination {
  width: 25px;
  height: 4px;
  background-color: #c9c1f1;
  border-radius: 100px;
  transition: all 0.3s;
  cursor: pointer;
}

.chart-pagination-active {
  width: 60px;
  background-color: #6b43b5;
}
