    15px 135px 0 -30px #fff, -15px 135px 0 -30px #fff, -35px 138px 0 -35px #fff, 35px 138px 0 -35px #fff,

    0 200px 0 5px #7A08FA, -38px 200px 0 0px #7A08FA, 38px 200px 0 0px #7A08FA;
}

.monster:before {
  content: '';
  display: block;
  width: 150px;
  height: 50px;
  background: #fff;
  border-radius: 100%;
  position: absolute;
  top: 110px;
  left: 0;
  right: 0;
  margin: auto;

  box-shadow: inset 0 -22px 0 0 #000
}

