.left-bar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 36px;
  height: 687px;
  box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.2);
  border-radius: 7px;
  padding: 20px 30px;
}

.text-rotate {
  text-wrap: nowrap;
  white-space: nowrap;
  transform: rotate(-90deg);
}

div.ant-tabs.ant-tabs-top.css-dev-only-do-not-override-frcp1j > div.ant-tabs-nav > div.ant-tabs-nav-wrap {
  border: none;
}

.mouse-hover:hover {
  cursor: pointer;
}

/* Automated board form */
.radio-btn-label:has(.radio-btn-input:checked) {
  color: #6b43b5 !important;
}

.select-input {
  background-color: "white";
  border: "1px solid #6B43B5";
}

.custom-scroll {
  scrollbar-width: thin;
  scrollbar-color: #6b43b5 transparent;
}

.audience-filter-item-hover {
  margin-right: -10px;
  transition: all 0.2s;
  cursor: pointer;
  padding: 3px;
  border-radius: 100px;
}

.audience-filter-item-hover:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.gender-radio:has(input[type="radio"]:checked) {
  color: #6b43b5;
}

.select-input-no-border > .ant-select-selector {
  border: none !important;
}

.select-input-no-border > .ant-select-selector:focus {
  border: none !important;
}

.custom-style-selection > .ant-select-selector {
  border-radius: 10px !important;
  border: 1px solid #6b43b5 !important;
  padding: 22px !important;
}

.custom-style-selection-manual-board > .ant-select-selector {
  border-radius: 10px !important;
  border: 1px solid #6b43b5 !important;
  padding: 7px !important;
}
.custom-style-selection-insurance-filter > .ant-select-selector {
  border-radius: 7px !important;
  padding: 18.5px 15px !important;
  border: 1px solid #6b43b5 !important;
}

.select-input-placeholder
  + .handle-select-placeholder
  > .ant-select-selector
  > span.ant-select-selection-wrap
  > span.ant-select-selection-search
  > input.ant-select-selection-search-input:invalid {
  visibility: visible;
}

.oral-health-score-checkbox:has(input[type="checkbox"]) {
  color: #848696;
}

.oral-health-score-checkbox:has(input[type="checkbox"]:checked) {
  color: #6b43b5;
}

label.ant-checkbox-wrapper.ant-checkbox-wrapper-disabled.custom-action-checkbox.css-dev-only-do-not-override-frcp1j
  > span.ant-checkbox.ant-wave-target.css-dev-only-do-not-override-frcp1j.ant-checkbox-disabled
  > span.ant-checkbox-inner {
  border-radius: 100px !important;
  width: 20px !important;
  height: 20px !important;
}

.customize-treatment-tabs > .ant-tabs-nav {
  /* To remove the space between tabs and the content of tab  */
  margin-bottom: 0 !important;
}

.customize-treatment-tabs > .ant-tabs-nav > .ant-tabs-nav-wrap {
  padding: 0 10px;
}

/* Removing bottom border */
.customize-treatment-tabs > .ant-tabs-nav::before {
  border: none;
}

.customize-treatment-tabs > .ant-tabs-nav > .ant-tabs-nav-wrap > .ant-tabs-nav-list > .ant-tabs-tab.ant-tabs-tab-active {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.15) !important;
}

.customize-treatment-tabs > .ant-tabs-content-holder {
  padding: 20px;
  border: none;
  border-radius: 0 10px 10px 10px;
  border-top: none;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.15);
  margin: 0 11px;
}

.audience-modal-close-btn-hover {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
}

.audience-modal-close-btn-hover:hover {
  background-color: rgba(0, 0, 0, 0.06);
}

.custom-divider-background-color::before,
.custom-divider-background-color::after {
  background-color: #c9c1f1;
}

/* Email template */
.text-email-template > .ant-card-body {
  padding: 10px;
  display: flex;
}

.dynamic-select-item {
  height: 26px;
}

.dynamic-select-item > .ant-select-selector {
  border-radius: 8px 0 8px 8px;
  border: 1px solid #6b43b5 !important;
}

.dynamic-select-item > .ant-select-selector > span.ant-select-selection-wrap > span.ant-select-selection-placeholder {
  font-size: 10px;
}

/* Draft.js text editor */
.custom-email-editor {
  line-height: 28px;
}
