.location-filter-map-container {
  position: relative;
}

.location-filter-map {
  width: 732px;
  height: 400px;
  margin-bottom: 30px;
}

.location-filter-map-search {
  border-radius: 10px;
  border: 1px solid #6b43b5;
  padding: 10px 20px;
  width: 90%;
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  background-color: #fff;
}

.audience-filters-date-input {
  width: 100%;
  border-radius: 7px;
  padding: 6px 12px;
  border: 1px solid #6b43b5;
}
